<template>
  <div>
      <div class="container">
          <div class="row">
              <div class="col s12 m8 offset-m2">
                  <div class="login card-panel red lighten-2 white-text center">
                      <h3>Login</h3>
                      <form >
                        <div class="input-field">
                            <i class="material-icons prefix">email</i>
                            <input v-model="email" type="email" name="" id="email">
                            <label class="white-text"  for="email">Email</label>
                        </div>
                        <div class="input-field">
                            <i class="material-icons prefix">lock</i>
                            <input v-model="password" type="password" id="password" >
                            <label class="white-text" for="password">Password</label>
                        </div>
                        <button v-on:click="login" class="btn btn-large grey lighten-4 black-text">Login</button>
                      </form>
                      <ul class="center sampleEmail ">

                        <li>Email : harman@gmail.com</li>
                         <li>Pass : hellothere</li>
                      </ul>
                  </div>
              </div>
          </div>
      </div>
  </div>
</template>


<script>
import firebase from 'firebase';
export default {
  name: 'login',
  data: function() {
    return {
      email: '',
      password: ''
    };
  },
  methods: {
    login: function(e) {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          user => {
            alert(`You are logged in as ${user.email} `);
            this.$router.go({ path: this.$router.path });
          },
          err => {
            alert(err.message);
          }
        );
      e.preventDefault();
    }
  }
};
</script>
<style scope>
.sampleEmail {
  margin-top: 20px;
}
</style>



