<template>
  <div id="dashboard">
        <ul class="collection with-header">
          <li class="collection-header">
            <h4>Employees</h4>
            <li v-for="employee in employees" v-bind:key="employee.id" class="collection-item">
              <div class="chip">{{employee.department}}</div>
            {{employee.employee_id}} {{employee.name}}
            <router-link class="secondary-content"   v-bind:to="{name: 'view-employee' , params : {
              employee_id: employee.employee_id}}" >
              <i class="fas fa-eye"></i>
              </router-link>
            </li>
        </ul>
        <div class="fixed-action-btn ">
        <router-link to="/new" class="btn-floating btn-large red lighten-2 "><i class="fas fa-plus"></i> </router-link>
      </div>
  </div>
</template>

<script>
import db from './firebaseinit';
export default {
  name: 'dashboard',
  data() {
    return {
      employees: []
    };
  },
  created() {
    db
      .collection('employees')
      .orderBy('department')
      .get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const data = {
            id: doc.id,
            employee_id: doc.data().employee_id,
            name: doc.data().name,
            department: doc.data().department,
            position: doc.data().position
          };
          this.employees.push(data);
        });
      });
  }
};
</script>



