<template>
<nav>
  <div class="nav-wrapper ">
    <div class="container">
      <router-link to="/" class="brand-logo left">Vue Firebase</router-link>
      <ul class="right">
        <li v-if="isLoggedIn">
          <span class="email">Hello <i class="far fa-smile"></i>  {{currentuser}} </span>
        </li>
        <li v-if="isLoggedIn" >
          <router-link to="/">Dashboard</router-link>
        </li>
        <li v-if="!isLoggedIn" >
          <router-link to="/login">Login</router-link>
        </li>
        <li v-if="!isLoggedIn">
          <router-link to="/register">Register</router-link>
        </li>
        <li v-if="isLoggedIn" >
          <button v-on:click="logout" class="btn clack">Logout</button>
        </li>
      </ul>
    </div>
  </div>
</nav>
</template>
<script>
import firebase from 'firebase';
export default {
  name: 'navbar',
  data() {
    return {
      isLoggedIn: false,
      currentuser: false
    };
  },
  created() {
    if (firebase.auth().currentUser) {
      this.isLoggedIn = true;
      this.currentuser = firebase.auth().currentUser.email;
    }
  },
  methods: {
    logout: function() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.go({ path: this.$router.path });
        });
    }
  }
};
</script>
<style scoped>
.email {
  padding-right: 10px;
}
</style>



